import React from 'react';
const SearchBox=({searchfields,searchChange})=>{
    return(
        <div className='pa2'>
            <input 
            className='pa3 ba b--green bg-lightest-blue' 
            type='search' 
            placeholder = "searchRobot"
            onChange = {searchChange}
            />
            
        </div>
    );
}
export default SearchBox;